.searchContactForm {	
  width: 600px;	
  margin-left: auto;	
  margin-right: auto;	
  margin-bottom: 20px;	
}	

.searchLabel {	
  text-align: center;	
  margin-bottom: 10px;	
}	


.d{	
  position: relative;	
}	
.searchInput {	
  width: 588px;	
  height: 30px;	
  padding: 5px;	
  border: 1px solid #ccc;	
  border-radius: 4px;	
}	

.deleteSvgIcon {	
  position: absolute;	
  padding: 10px;	

  right: 10px;	
  top: calc(50% + 5px);	
  transform: translateY(calc(-50% - 5px));	
}	

.deleteSvgIcon:hover{	
  cursor: pointer;	
}